.cartItem
    display: flex
    align-items: center
    padding: 50px 0
    border-bottom: 1px dashed rgb(63, 78, 101)
    &:first-child
        border-top: 1px dashed rgb(63, 78, 101)
    >img
        width: 281px
        height: 191px
        margin-right: 20px
        cursor: pointer
    &__info
        max-width: 480px
        padding-right: 80px
        h5
            position: relative
            font-weight: 400
            font-size: 12px
            line-height: 130%
            color: #3F4E65
            margin-bottom: 9px
            padding-left: 22px
            img
                position: absolute
                top: -1px
                left: 0
        h2
            font-weight: 700
            font-size: 30px
            line-height: 36px
            color: #111111
            margin-bottom: 10px
        p
            font-weight: 300
            font-size: 12px
            line-height: 150%
            color: #3F4E65

    &__count
        padding: 0 44px
        display: flex
        align-items: center
        button
            outline: none
            cursor: pointer
            border: none
            width: 34px
            height: 20px
            background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%)
            border-radius: 1000px
        span
            width: 45px
            display: block
            text-align: center
    &__container
        display: flex
        align-items: center
        >h2
            font-weight: 700
            font-size: 30px
            line-height: 130%
            color: #111111
            width: 200px
            text-align: center
        >button
            cursor: pointer
            position: relative
            width: 59px
            height: 59px
            border: none
            background: #FFC85E
            border-radius: 79px
            margin-left: 44px
            img
                position: absolute
                top: 20px
                left: 20px

@media (max-width: 1410px)
    .cartItem
        &__info
            max-width: 300px
            padding-right: 0px
            h5
                font-size: 10px
                margin-bottom: 9px
                padding-left: 22px
                img
                    top: -1px
                    left: 0
            h2
                font-size: 26px
                margin-bottom: 10px
            p
                font-size: 10px
        &__count
            padding: 0 11px
            display: flex
            align-items: center
            button
                width: 34px
                height: 20px
            span
                width: 35px
        >h2
            font-size: 26px
            width: 200px
        >button
            position: relative
            width: 59px
            height: 52px
            border-radius: 79px
            margin-left: 0px
            img
                top: 16px
                left: 16px

@media (max-width: 900px)
    .cartItem
        padding: 20px 0
        flex-direction: column
        >img
            margin-right: 0
        &__info
            max-width: 100%
            padding-right: 0px
            h5
                font-size: 10px
                margin-bottom: 9px
                padding-left: 22px
                img
                    top: -1px
                    left: 0
            h2
                font-size: 26px
                margin-bottom: 10px
            p
                font-size: 10px
        &__count
            padding: 0 11px
            display: flex
            align-items: center
            button
                width: 34px
                height: 20px
            span
                width: 35px
        &__container
            margin-top: 15px
            >h2
                font-size: 26px
                width: 200px
            >button
                position: relative
                width: 59px
                height: 59px
                border-radius: 79px
                margin-left: 0px
                img
                    top: 20px
                    left: 20px

@media (max-width: 490px)
    .cartItem
        flex-direction: column
        &__info
            max-width: 100%
            padding-right: 0px
            h5
                font-size: 10px
                margin-bottom: 9px
                padding-left: 22px
            h2
                font-size: 20px
                margin-bottom: 10px
            p
                font-size: 12px
        &__count
            padding: 0 0px
            button
                width: 34px
                height: 20px
            span
                font-size: 14px
                width: 35px
        &__container
            margin-top: 15px
            >h2
                font-size: 16px
                width: 100px
            >button
                position: relative
                width: 59px
                height: 59px
                border-radius: 79px
                img
                    top: 20px
                    left: 20px
